import { useState, useCallback } from "react";
import { Toaster } from "react-hot-toast";
import "./App.css";

import Home from "./steps/Home";
import Name from "./steps/Name";
import Phone from "./steps/Phone";
import Code from "./steps/Code";
import Finish from "./steps/Finish";

function App() {
  const [step, setStep] = useState(0);
  const [id, setId] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");

  const prev = useCallback(() => {
    setStep((step) => step - 1);
  }, []);
  const next = useCallback(() => {
    setStep((step) => step + 1);
  }, []);

  return (
    <div className="container">
      {step === 0 && <Home next={next} onSubmit={(id) => setId(id)} />}
      {step === 1 && (
        <Name
          prev={prev}
          next={next}
          defaultValue={name}
          onSubmit={(name) => setName(name)}
        />
      )}
      {step === 2 && (
        <Phone
          prev={prev}
          next={next}
          defaultValue={phone}
          onSubmit={(phone) => setPhone(phone)}
        />
      )}
      {step === 3 && (
        <Code prev={prev} next={next} name={name} phone={phone} id={id} />
      )}
      {step === 4 && <Finish prev={prev} next={next} />}
      <Toaster
        toastOptions={{
          style: {
            background: "#1e2734",
            color: "#fff",
          },
        }}
      />
    </div>
  );
}

export default App;
