import { useState, useCallback } from "react";
import axios from "../lib/axios";
import toast from "react-hot-toast";

import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Title,
	InputMask,
} from "../Form";

export default ({ prev, next, defaultValue, onSubmit }) => {
	const [loading, setLoading] = useState(false);
	const [value, setValue] = useState(defaultValue);

	const onChange = useCallback((e) => {
		setValue(e.target.value);
	}, []);

	const sendCode = useCallback(() => {
		return axios({
			method: "post",
			url: "/SendVerCode",
			data: { phone: value },
		})
			.then(({ data }) => {
				data.text && toast.error(data.text);
				if (!data) {
					onSubmit(value);
					next();
				}
			})
			.catch((e) => toast.error("Неизвестная ошибка\n" + e));
	}, [value]);

	const submit = useCallback(() => {
		setLoading(true);
		axios({
			method: "post",
			url: "/CheckPhone",
			data: { phone: value },
		})
			.then(async ({ data }) => {
				data.text && toast.error(data.text);
				if (data.correct) await sendCode();
			})
			.catch((e) => toast.error("Неизвестная ошибка\n" + e))
			.finally(() => setLoading(false));
	}, [value]);

	return (
		<Container>
			<Body>
				<Title>Введите номер телефона</Title>
				<InputMask
					disabled={loading}
					mask="+7 (999) 999-99-99"
					placeholder="+7 (999) 999-99-99"
					value={value}
					onChange={onChange}
				/>
			</Body>
			<Footer loading={loading}>
				<Steps step={2} total={3} />
				<ButtonsGroup>
					<Button
						disabled={loading}
						secondary
						icon="arrow-left"
						onPress={prev}
					/>
					<Button
						disabled={loading || !value.length}
						iconRight="arrow-right"
						onPress={submit}
					>
						Далее
					</Button>
				</ButtonsGroup>
			</Footer>
		</Container>
	);
};
