import { useState, useCallback } from "react";
import axios from "../lib/axios";
import toast from "react-hot-toast";

import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Title,
	Input,
} from "../Form";

export default ({ prev, next, name, phone, id }) => {
	const [loading, setLoading] = useState(false);
	const [code, setCode] = useState("");

	const onChange = useCallback((e) => {
		setCode(e.target.value);
	}, []);

	const submit = useCallback(() => {
		setLoading(true);
		axios({
			method: "post",
			url: "/GetKupon",
			data: { name, phone, code, id },
		})
			.then(async ({ data }) => {
				console.log(data);
				if (data.error) data.text && toast.error(data.text);
				else next();
			})
			.catch((e) => {
				toast.error("Неизвестная ошибка\n" + e);
			})
			.finally(() => setLoading(false));
	}, [name, phone, code, id]);

	return (
		<Container>
			<Body>
				<Title>Введите код из смс</Title>
				<Input
					type="number"
					placeholder="12345"
					maxLength="5"
					value={code}
					onChange={onChange}
				/>
			</Body>
			<Footer loading={loading}>
				<Steps step={3} total={3} />
				<ButtonsGroup>
					<Button
						disabled={loading}
						secondary
						icon="arrow-left"
						onPress={prev}
					/>
					<Button
						disabled={loading || !code.trim().length}
						iconLeft="check"
						onPress={submit}
					>
						Последний шаг
					</Button>
				</ButtonsGroup>
			</Footer>
		</Container>
	);
};
