import { useState, useEffect } from "react";
import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Text,
} from "../Form";
import BarLoader from "react-spinners/BarLoader";
import axios from "../lib/axios";
import toast from "react-hot-toast";
import queryString from "query-string";

import "./Home.css";

const params = !!window.location.search
	? queryString.parse(window.location.search)
	: {};

export default ({ next, onSubmit }) => {
	const [loading, setLoading] = useState(true);
	const [id, setId] = useState("");
	const [error, setError] = useState();

	useEffect(() => {
		if (!params?.id) {
			setError("Неизвестная акция");
			setLoading(false);
			return;
		}

		onSubmit(params.id);
		setTimeout(() => setLoading(false), 1000);

		return;

		const id = axios({
			method: "post",
			url: "/CheckID",
			data: { id: params.id },
		})
			.then(async (response) => {
				const { data } = response;
				if (data.error) data.text && toast(data.text);
				else next();
				return response;
			})
			.catch((e) => {
				setError("Акция не найдена");
			})
			.finally(() => setLoading(false));
	}, []);

	if (loading)
		return (
			<Container>
				<Body center>
					<BarLoader color="#323b4c" />
					<Text>Пожалуйста, подождите...</Text>
				</Body>
			</Container>
		);

	if (error)
		return (
			<Container>
				<Body center>
					<Text>{error}</Text>
				</Body>
			</Container>
		);

	return (
		<Container>
			<Body center>
				<div className="home-hr" />
				<div className="home-title">Получить купон с ИМ ЗО</div>
			</Body>
			<Footer>
				<Button primary iconLeft="check" onPress={next}>
					Начать
				</Button>
			</Footer>
		</Container>
	);
};
